
.video-controller {
  position: relative;
  overflow: hidden;
}
.video-controller video {
  max-width: 100%;
  min-width: 100%;
}
.video-controller-action {
  left: 0;
  top: 0;
  width: 100%;
  height: 99%;

  position: absolute;
  z-index: 100;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: rgba(0, 0, 0, 0.5);
  opacity: 1;

  transition: opacity 0.3s;
}
.video-controller-action img {
  width: 48px;
  height: 48px;
  opacity: 1;
}
.video-controller-action-on {
  opacity: 0;
}
