
html,
body {
  font-family: "Microsoft YaHei", sans-serif;
}
.container {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}
.my-swiper-container {
  height: 100%;
  width: 100%;
  visibility: hidden;
}
.swiper-wrapper-detail {
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
}
.swiper-wrapper-detail section {
  position: relative;

  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.my-swiper-container-horizontal {
  height: 100%;
  width: 100%;

  position: relative;
}
.swiper-pagination .swiper-pagination-bullet {
  background: #aaa;
}
.swiper-pagination .swiper-pagination-bullet-active {
  background: white;
}
.tips {
  position: fixed;
  left: 0;
  top: 0;
  z-index: -999;
  visibility: visible;

  width: 100%;
  height: 100%;

  background-color: #333;

  display: flex;
  align-items: center;
  justify-content: center;
}
.tips div {
  background-color: white;
  padding: 48px;
  border-radius: 4px;
}
@media screen and (max-width: 1000px) {
.my-swiper-container {
    visibility: visible;
}
.tips {
    visibility: hidden;
}
}
